import {Models} from './date/data-types';
import Vue from 'vue';
import Component from 'vue-class-component';
import {Prop} from 'vue-property-decorator';

@Component({
  name: 'DatePickerProps'
})
export default class DatePickerProps extends Vue {
  /** default: today */
  @Prop({})
  public defaultDate?: Date;
  /**  */
  @Prop({})
  public endDate?: Date;
  /**  */
  @Prop({})
  public getDateExtra?: (date: Date, currentValue?: Date[]) => Models.ExtraData;
  /** default: false */
  @Prop({type: Boolean})
  public infiniteOpt?: boolean;
  /** default: 6 */
  @Prop({type: Number})
  public initialMonths?: number;
  /**  */
  @Prop({})
  public locale?: Models.Locale;
  /**  */
  @Prop({type: Date})
  public maxDate?: Date;
  /**  */
  @Prop({type: Date})
  public minDate?: Date;
  /**  */
  @Prop({})
  public onSelectHasDisableDate?: (date: Date[]) => void;
  /** (web only)  */
  @Prop({type: String})
  public prefixCls?: string;
  /**  */
  @Prop({})
  public rowSize?: 'normal' | 'xl';
  /**  */
  @Prop({type: Date})
  public startDate?: Date;
  /** default: rangeone: range:  */
  @Prop({})
  public type?: 'one' | 'range';
}
